import '../styles/Header.scss';
import SVG from '../assets/svg';
import ilustration from '../assets/ilustration.svg';
import { useEffect } from 'react';
import { gsap } from "gsap";
import { DrawSVGPlugin } from "gsap/DrawSVGPlugin";

const Header = () => {
  useEffect(() => {
    gsap.registerPlugin(DrawSVGPlugin)
    
    gsap.timeline()
      .to("body",{overflow: "hidden"})

      .from(".prefix__st2", {drawSVG: "50% 50%",duration: 4})

      .to(".prefix__st2", {fill: '#F1F1F1'})

      .to("body", {overflowY:"visible"})

      .to(".ilustration", {y:-100, yoyo:true, repeat:-1, duration:4})
    

  }, [])

  return (
    <div className="HeaderSection">
      <div className="Navigation">
        <ul>
          <li><a href="#offer">OFERTA</a></li>
          <li><a href="#gallery">REALIZACJE</a></li>
          <li><a href="#contact">KONTAKT</a></li>
        </ul>
      </div>
      <div className='Logo-container'>
        <div className='bg'></div>
        <div className='bg-mobile'></div>
        <div className='bg-mobile-s'></div>
        <SVG className='svg'/>
        <img src={ilustration} className='ilustration'></img>
        <div className='text-zone'>
        <h1>Kompleksowe wykończenia wnętrz.</h1>
        <p>Zaufaj firmie z doświadczeniem! Nasza działalność opiera się na tradycji zapoczątkowanej w 1994 roku. Od tamtej pory podązamy za aktualnymi trenadmi i wzornictwem, by inwestorzy byli nieprzerwanie zadowoleni. Remonty to nasze hobby, dzięki naszej pasji i doświadczeniu gwarantujemy Państwu satysfakcję.</p>
        </div>
      </div>
    </div>
  );
}

export default Header;